@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
body {
  margin: 0;
}
html{
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  position: fixed;
  box-shadow: 0 5px 25px 0 rgba(226, 0, 116, 0.5);
  background-color: #151820;
  color: #ffffff;
  width: 100%;
  display: flex;
  justify-content: space-between;
  height: 4.5rem;
  z-index: 3;
}

.leftHead {
  font-family: "Roboto", sans-serif;
}

.logo{
  width: 2.5rem; margin-right: 1rem;
}
.logo2{
  width: 1rem; margin-left: 1rem;
}

.leftHead{
  margin-right: 13rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.8rem;
  font-weight: 600;
}
.rightHead {
  margin-left: 13rem;
  display: flex;
  align-items: center;
  justify-content: center;
  /* margin-left: 2rem; */
}
.rightHead-1 {
  font-family: "Ardela Edge";
  display: flex;
  justify-content: center;
  align-items: center;
  letter-spacing: 0.4rem;
}

.rightHead-2 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 5rem;
}

ul {
  height: 4.5rem;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 0.9rem;
}

li {
  height: 4.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 0.5rem;
  font-weight: 600;
}

li:hover{
  height: 4.4rem;
  background-image: linear-gradient(to top, #72134b, #1d212c);
}
.lH1, .lH2{
    margin: 0 .5rem;
}
.lH1{
  text-decoration: underline;
}

.link-CSS{
  text-decoration: none;
  color: #ffffff;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.EmptyDiv{
  color: white;
}
body {
  font-family: "Roboto";
  height: 100vh;
  background-image: linear-gradient(to top, #2f3545, #1d212c);
}

#root {
  height: 100%;
}

.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}


.content{
  display: flex;
  /* justify-content: center;
  align-items: center; */
  height: calc(100vh - 13.5rem);
  margin-top: 9rem;
  width: calc(100% - 26rem);
  margin-left: 13rem;
  margin-right: 13rem;
}
.allSafes{
    background-color: #151820;
    width: 30%;
    color: white;
}
.safesearch{
    font-family: Roboto;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding:1rem;
    border: 1px solid #20232e;
}
  
.safeinput{
width: 15rem;
color: #d8d8d8;
font-size: 1rem;
font-family: Roboto;
background-color: #20232e;
border: none;
}

textarea:focus, input:focus{
outline: none;
}

.firstsafe{
    margin-top: 10rem;
    display: flex;
    justify-content:space-around;
    flex-direction: column;
    align-items: center;
}
.Afterfirstsafe{
    display: flex;
    justify-content:flex-start;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.innerCardContainer{
    width: 100%;
}
.innerCard{
    margin-bottom: 0.1rem;
    display: flex;
    padding: 1.5rem;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #2a2e3e;
}
.innerCard:hover{
    background-image: linear-gradient(to right, #72134b, #1d212c);
}
.innerCard-dataL{
    margin-right: 6rem;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: left;
    font-size: 0.8rem;  
    font-weight: 400;  
}
.innerCard-dataR{
    display: flex;
    align-items: center;
    margin-left: 1rem;
    font-size: 0.8rem;  
    font-weight: 400;
}

h4{
    z-index:2;
    font-size: 0.8rem;
    margin-top: -3rem;
    margin-bottom: 1rem;
}

.changecolor{    
color: #5e627c;
}
.AddbuttonSafe{
    position: fixed; margin-top: 60vh;
}
.vaultdel{
    width: 2rem;
}
.innerCard-dataR img{
    padding: 0.5rem;
    margin-left: 0.5rem;
    background-color: rgb(122, 122, 122, 0.1);
    border-radius: 50%;
}
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 4;
}
.popupFolder{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 4;
  color: white
}
form {
  opacity: 1;
  padding: 3rem;
  height: calc(100% - 17rem);
  width: 45rem;
  background-color: #2a2e3e;
  display: flex;
  flex-direction: column;
}
.folderform{height: calc(100% - 40rem);
  height: auto;
}
.popup_desc {
  margin: 2rem 0rem;
  display: flex;
}
.popup_desc p{
  font-family: Helvetica;
  font-size: 0.9rem;
  color: #c4c4c4;
  text-align: left;
  padding-left: 1rem;
}
form h2{
  font-family: Roboto;
  letter-spacing: 0.1rem;
}

  .SafeInputs {
    display: flex;
    flex-direction: column;
  }
.SafeInputs label{
font-size: 0.8rem;
padding-top: 0.5rem;
padding-bottom: 0.5rem;
}
input, select, option {
  height: 3rem;
  padding: 1rem;
  color: #000;
  background-color: #fff;
  margin-bottom: 0.5rem;
}
textarea{
  height: 5rem;
  padding: 1rem;
}
.SafeInputs p{
  font-size: 0.6rem;
  color: #8b8ea6;
  margin-top: 0.3rem;
}
.popupbuttons {
  display: flex;
  margin-top: 1rem;
  justify-content: flex-end;
}
  
button {
  padding: 0.5rem 1.5rem;
  margin-left: 1rem;
  font-size: 1rem;
  color:#e20074;
  font-weight: 600;
  border:none;
}

button:hover{
  color: white;
  background-color:#e20074;
}


.SecrectSafes {
  background-image: linear-gradient(to top, #151820, #2c3040);
  width: 70%;
}

.bx {
  position: absolute;
  left: 0.4rem;
  opacity: .5;
}
.logo3{
  width: 1.2rem;
  margin-left: 0.3rem;
}


.SafeBanner {
  height: 10.5rem;
  object-fit: contain;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  padding-left: 35%;
  padding-right: 5%;
  width: 100%;
  margin-top: -8px;
  width: 100%;
  z-index: 4;
  color: white;
  background-repeat: no-repeat;
}
#safeName {
  font-size: 1.4rem;
  text-align: left;
  color: white;
  font-weight: 500;
  font-family: "Roboto", sans-serif;
  margin-bottom: 0.5rem;
}

#safeDescription {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 0.75rem;
  text-align: left;
  color: #c4c4c4;
  line-height: 1.29;
}


.banner{
  width: 100%; margin-top: -0.55rem; 
}
.DisplaySafe-buttons{
  display: flex;
  margin-top: -0.55rem; 
  justify-content:space-between;
  align-items: center;
  flex-direction: row;
  border-bottom: 1px solid white;
  margin: 0rem 2rem;
  color: #5e627c;
  font-weight: 600;
  padding:1rem 0rem;
}
.DisplayRight a{
  padding:1rem 1rem;
}

.DisplayLeft a:hover{
color: #fff;
}
.DisplayRight a:hover{
  color: #fff;
  border-bottom: 2px solid #e20074;
}
.DisplayLeft{  
  display: flex;
  justify-content:center;
  align-items: center;
}
.DisplaySafe-SafeArea{
  display: flex;
  width: 100%;
  flex-direction: column;  
  font-family: Helvetica;
  justify-content:space-around;
  align-items: center;
