  padding: 2rem 2rem;
  flex-direction: column;
  font-size: 0.6rem;
  color: #5e627c;
}

.DisplaySafe-SafeArea a{
  font-size: 1rem;
  display: flex;
  color: #fff;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin: 1rem;
  padding: 1rem;
  background-color: #454c5e;
}
.logo4{
  margin-right: 0.5rem;
}

.folderContainer{
  width: 100%;
}
.foldercardparent{

  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  background-color: #1c1f29;
  padding: 0.7rem 0.5rem;

}
.foldercard h2{
  font-size: 1.2rem;
  margin-left: 1rem;
}
.foldercardparent:hover{
  background-image: linear-gradient(to right, #72134b, #1d212c);
